import { axiosClient } from "@rp/react/axios-client"
import { CONTENT_PACKAGES_ROUTE } from "../content.constants"
import { ContentPackage } from "../content.types"

type getContentPackageByID = {
  id: string
}

async function getContentPackageByID({ id }: getContentPackageByID): Promise<ContentPackage> {
  const { data } = await axiosClient.get<ContentPackage>(`${CONTENT_PACKAGES_ROUTE}/${id}`)

  return data
}

export { getContentPackageByID }
